:root {
    --primary: #232f3e;
    --secondary: #f3a847;
    --tertiary: #b12704;
    --light: #fafafa;
    --dark: #272f32;
    --error: rgb(228, 46, 1);
    --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem);
}

* {
    box-sizing: border-box;
}

html,
body {
    min-height: 100%;
    width: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Karla', sans-serif;
    font-weight: 400;
    line-height: 1.5;
    font-size: 16px;
    color: #272f32;
    color: var(--dark);
    background: linear-gradient(-45deg, #18c936, #d69cd6, #edf2f3, #23d5ab);
    background-size: 400% 400%;
    -webkit-animation: gradient 15s ease infinite;
            animation: gradient 15s ease infinite;
}

@-webkit-keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Rubik', sans-serif;
    font-weight: 500;
    margin: 5px 0;
    line-height: 1.25;
}

h1 {
    font-size: 2.25rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.75rem;
}

h4 {
    font-size: 1.5rem;
}

h5 {
    font-size: 1.25rem;
}

h6 {
    font-size: 1rem;
}

p {
    font-size: 1.1rem;
    margin-top: 0;
}

a {
    color: #b12704;
    color: var(--tertiary);
    text-decoration: none;
    font-weight: bold;
}

a:hover {
    text-decoration: underline;
}

header {
    background: #232f3e;
    background: var(--primary);
}

header a {
    color: #fafafa;
    color: var(--light);
}

button {
    font-size: 1rem;
    text-decoration: none;
    padding: 0.25rem 1rem;
    border-radius: 0.5rem;
    border: none;
    outline: none;
    cursor: pointer;
    margin: 0 1px;
    background-color: #f3a847;
    background-color: var(--secondary);
}

button:hover {
    opacity: 0.8;
}

button[disabled] {
    cursor: default;
    opacity: 0.8;
}

img {
    max-width: 100%;
}

span[role='img'] {
    cursor: default;
}

form {
    width: 40%;
}

input {
    font-size: 1rem;
    padding: 0.25rem;
    border: 1px solid #272f32;
    border: 1px solid var(--dark);
    outline: none;
    border-radius: 3px;
    transition: box-shadow 0.2s;
}

input:focus {
    border: 1px solid #f3a847;
    border: 1px solid var(--secondary);
    box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

.container {
    width: 85%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

.flex-row {
    display: flex;
    flex-wrap: wrap;
}

.space-between {
    justify-content: space-between;
}

.flex-end {
    justify-content: flex-end;
}

.mx-1 {
    margin-right: calc(1rem * 0.5);
    margin-right: var(--spacing-one);
    margin-left: calc(1rem * 0.5);
    margin-left: var(--spacing-one);
}

.mx-2 {
    margin-right: calc(1rem);
    margin-right: var(--spacing-two);
    margin-left: calc(1rem);
    margin-left: var(--spacing-two);
}

.my-1 {
    margin-top: calc(1rem * 0.5);
    margin-top: var(--spacing-one);
    margin-bottom: calc(1rem * 0.5);
    margin-bottom: var(--spacing-one);
}

.my-2 {
    margin-top: calc(1rem);
    margin-top: var(--spacing-two);
    margin-bottom: calc(1rem);
    margin-bottom: var(--spacing-two);
}

.px-1 {
    padding-right: calc(1rem * 0.5);
    padding-right: var(--spacing-one);
    padding-left: calc(1rem * 0.5);
    padding-left: var(--spacing-one);
}

.px-2 {
    padding-right: calc(1rem);
    padding-right: var(--spacing-two);
    padding-left: calc(1rem);
    padding-left: var(--spacing-two);
}

.py-1 {
    padding-top: calc(1rem * 0.5);
    padding-top: var(--spacing-one);
    padding-bottom: calc(1rem * 0.5);
    padding-bottom: var(--spacing-one);
}

.py-2 {
    padding-top: calc(1rem);
    padding-top: var(--spacing-two);
    padding-bottom: calc(1rem);
    padding-bottom: var(--spacing-two);
}

.card {
    width: 25%;
    text-align: center;
}

.error-text {
    color: rgb(228, 46, 1);
    color: var(--error);
}

@media screen and (max-width: 768px) {
    .card {
        width: 100%;
    }
}

.namefont {
    text-transform: uppercase;
    background-image: linear-gradient(
        -225deg,
        #231557 0%,
        #44107a 29%,
        #ff1361 67%,
        #fff800 100%
    );
    background-size: auto auto;
    background-clip: border-box;
    background-size: 200% auto;
    color: #fff;
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-animation: textclip 2s linear infinite;
            animation: textclip 2s linear infinite;
    display: inline-block;
}

@-webkit-keyframes textclip {
    to {
        background-position: 200% center;
    }
}

@keyframes textclip {
    to {
        background-position: 200% center;
    }
}

.cart {
  position: fixed;
  top: 0;
  right: 0;
  min-width: 20%;
  max-width: 30%;
  max-height: 60%;
  background-color: var(--light);
  overflow: auto;
  padding: 0.5rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.5);
  border-bottom-left-radius: 0.5rem;
}

.cart h2 {
  font-size: 1.5rem;
  border-bottom: 1px solid var(--dark);
  padding-bottom: 0.5rem;
  margin: 1rem 0;
}

.cart .close {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  cursor: pointer;
  color: var(--tertiary);
}

.cart .close:hover {
  text-decoration: underline;
}

.cart-closed {
  position: fixed;
  top: 2%;
  right: 1%;
  font-size: 2rem;
  cursor: pointer;
  background-color: var(--secondary);
  border-radius: 50%;
  padding: 0.25rem;
  width: 50px;
  height: 50px;
}

.cart-closed:hover {
  -webkit-transform: rotate(8deg);
          transform: rotate(8deg);
}

.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart input {
  width: 50px;
  padding: 0;
  margin: 0 0.5rem 1rem 0;
}

@media screen and (max-width: 768px) {
  .cart {
    position: relative;
    max-width: 100%;
    max-height: auto;
    box-shadow: none;
    background-color: inherit;
  }

  .cart-closed {
    position: relative;
    margin: 0 auto;
  }
}

